























import Vue from 'vue'
import { any, square, pow } from '@tensorflow/tfjs';
class BSpline{
  private _mode = -1;
  private _point: [number, number][] = [];
  private _points: [number, number][] = [];
  // point, axes
  private _node: number[] = [];
  private _k = 1;
  public addPoint(point: [number, number]){
    this._point.push(point);
    this.generateNodes();
  }
  public removePoint(index: number){
    this._point.splice(index, 1);
    this.generateNodes();
  }
  set Mode(val: number){
    this._mode = val;
    this.generateNodes();
  }
  set K(val: number){
    this._k = val;
    this.generateNodes();
  }
  get Points(){
    return this._point;
  }
  private generateNodes(){
    this._node = [];
    switch(this._mode){
      case 0:
        for(let i = 0; i < this._point.length+this._k+1; ++i){
          this._node.push(i);
          this._points = this._point.slice();
        }
        break;
      case 1:
        for(let i = 0; i < this._point.length+this._k+1; ++i){
          this._node.push(i);
        }
        for(let i = 0; i < this._k; ++i){
          this._node[i]  = this._node[this._k];
        }
        for(let i = 0; i < this._k; ++i){
          this._node[this._node.length-1-i] = this._node[this._node.length-this._k-1];
        }
        break;
      case 2:
        break;
      default:
        break;
    }
  }
  public getPoint(x: number): [number, number]|null{
    if(this._k >= this._point.length){
      return null;
    }
    if(this._mode == -1){
      return null;
    }
    if(x < 0 || x > 1){
      return null;
    }
    let u = 0;
    switch(this._mode){
      case 0:
        u = this._k + x * (this._node.length - 2*this._k - 1)
        break;
      case 1:
        u = this._k + x * (this._node.length - 2*this._k - 1);
        break;
      case 2:
        break;
    }
    let res: [number, number] = [0, 0]
    for(let i = 0; i < this._point.length; ++i){
      let k = this.getValue(i, this._k, u);
      res[0] += k*this._point[i][0];
      res[1] += k*this._point[i][1];
    }
    return res;
  }
  private getValue(i: number, k: number, x: number): number{
    if(k == 0){
      if(this._node[i] === this._node[i+1]){
        if(x === this._node[i])
          return 1;
      }
      if(x >= this._node[i] && x < this._node[i+1]){
        return 1;
      }
      return 0;
    }
    let first = 0;
    if(this._node[i+k]-this._node[i] == 0){
      first = this.getValue(i, k-1, x);
    }else{
      first = (x - this._node[i])/(this._node[i+k]-this._node[i])*this.getValue(i, k-1, x);
    }
    if(first === 1){
      return first;
    }
    let second = 0;
    if(this._node[i+k+1]-this._node[i+1] == 0){
      second = this.getValue(i+1, k-1, x);
    }else{
      second = (this._node[i+k+1]-x)/(this._node[i+k+1]-this._node[i+1])*this.getValue(i+1, k-1, x);
    }
    return first + second;
  }
}
export default Vue.extend({
  data(){
    return{
      splinemode: <number|string>"",
      ctx: <CanvasRenderingContext2D>{},
      width: 4,
      bspline: new BSpline(),
      k: 1,
      point: <[number, number]|null>null,
      stop: false
    }
  },
  watch:{
    splinemode(val: number|string){
      if(val === ""){
        this.bspline.Mode = -1;
      }else{
        this.bspline.Mode = <number>val;
      }
    },
    k(val: number){
      this.bspline.K = val;
    }
  },
  mounted(){
    let paper = <HTMLCanvasElement>document.getElementById('paper');
    paper.onmousedown = (ev)=>{
      if(ev.buttons == 1){
        for(let point of this.bspline.Points){
          if(Math.pow(point[0]-ev.offsetX, 2) + Math.pow(point[1]-ev.offsetY, 2) < 16){
            this.point = point
            break;
          }
        }
        if(this.point == null) this.bspline.addPoint([ev.offsetX, ev.offsetY]);
      }else{
        for(let i = 0; i < this.bspline.Points.length; ++i){
          let point = this.bspline.Points[i];
          if(Math.pow(point[0]-ev.offsetX, 2) + Math.pow(point[1]-ev.offsetY, 2) < 16){
            this.bspline.removePoint(i);
            break;
          }
        }
      }
      ev.preventDefault();
    };
    paper.onmousemove = (ev)=>{
      if(this.point){
        this.point[0] = ev.offsetX;
        this.point[1] = ev.offsetY;
      }
    }
    paper.onmouseup = (ev)=>{
      this.point = null;
    }
    paper.oncontextmenu = (ev)=>{
      ev.preventDefault();
    }
    this.ctx = paper.getContext('2d')!;
    this.animation();
  },
  methods:{
    animation(){
      this.ctx.clearRect(0, 0, 10000, 10000);
      for(let point of this.bspline.Points){
        this.ctx.fillStyle = 'red'
        this.ctx.fillRect(point[0]-this.width*0.5, point[1]-this.width*0.5, this.width, this.width);
        this.ctx.beginPath()
        let sample = 100;
        for(let i = 0; i <= sample; i++){
          let point = this.bspline.getPoint(i/sample)!;
          if(point == null){
            break;
          }
          if(i == 0){
            this.ctx.moveTo(point[0], point[1]);
          }else{
            this.ctx.lineTo(point[0], point[1]);
          }
        }
        // this.ctx.closePath();
        this.ctx.stroke();
      }
      if(this.stop) return
      requestAnimationFrame(this.animation)
    }
  },
  destroyed(){
    this.stop = true;
  }
})
